 @import "../_store-variables.less";
@import "../_store-mixins.less";


.fn-@{store-ns}-banner{
	.banner{
		overflow: hidden;

		&:before{
			padding-top: 32%;
		}
	}

	.bookmark{
		.absolute(0,2.8%,_,_);
		width: 50*@rex;
		background: rgba(0,0,0,0.2);
		text-align: center;
		color: #fff;
		border-radius: 0 0 5*@rex 5*@rex;
		padding:0 0 5*@rex 0;
		font-size:13*@rex;

		.iconfont{
			font-size: 26*@rex;
			line-height: 1.4;
			display: block;

			&:before{
				content: '\e60b';
			}
		}


		&ed{
			.iconfont{
				color: @ht-color;

				&:before{
					content: '\e60c';
				}
			}
		}
	}

	.nav{
		width: 100%;
		background: #fff;
		overflow: hidden;

		.item{
			float: left;
			border-right: 1*@rex solid #999;
			margin-top: 5*@rex;
			margin-bottom: 5*@rex;
			width: 25%;

			&:last-child{
				border-right: none;
			}

			> a{
				display: block;
				text-align: center;
				color: #333;

				&.index{
					color: @ht-color;
				}

				span{
					display: block;
					width: 100%;
					font-size:12*@rex;
					line-height: 1.5;
				}

				.num{
					font-family: arial;
					font-size:16*@rex;
				}
			}
		}
	}

	.logo{
		.absolute(_,_,10*@rex, 2%);
		width: 16%;

		img{
			box-shadow: 0 0 5*@rex #666
		}
	}

	.title{
		.absolute(_,_,10*@rex, 20%);
		max-width: unit(100 - 22, %);
		line-height: 2;
		color: #fff;
		background: rgba(0,0,0,0.2);
		padding: 0 8*@rex;
		border-radius: 8*@rex;
		font-size: 16*@rex;
		text-shadow: 0 1*@rex 3*@rex rgba(0,0,0,.4), 0 0 30*@rex rgba(0,0,0,.075);
	}

	.search-bar{
		width:93.6%;
		margin: 8*@rex auto;
		background: #fff;
		border-radius: 5*@rex;
		color: #999;
		text-align: center;
		font-size: 18*@rex;
		line-height: 1.8;
		display: block;

		span{
			// vertical-align: top;
			display: inline-block;
		}

		.iconfont{
			margin-right: 1%;
			font-size: 18*@rex;
		}
	}
}
