@import "../_store-variables.less";
@import "../_store-mixins.less";


.fn-@{store-ns}-new-product-main{
	@bd-color: #d5d5d5;
	@mar: 10;
	@pd: 10;

	.introduce-new{
	    margin: unit(@mar, px);
	    padding: unit(@pd, px 0);
	    text-align: center;
	    background-color: #fff;
	    font-size: 14px;
	    color: #666;
	    border-bottom: 1px solid @bd-color;
	}

	.item{
		overflow:hidden;

		.datetime_box{
			font-size: 14px;
		    color: #888;
		    padding-left: unit(@pd, px);
		}

		.item-list{
			padding: unit(@pd, px) unit(@pd, px) 0 0;
			overflow: hidden;

			.list{
				margin-bottom: unit(@mar, px) ;
			    float: left;
			    width: 50%;
			    padding-left: unit(@pd, px);


			    a{
			    	display: block;
			    	background: #fff;
			    	font-size: 14px;

			    	.glname{
			    		overflow: hidden;
					    text-overflow: ellipsis;
					    margin: unit(@mar / 2, px);
					    height: 36px;
					    line-height: 18px;
					}
			    }

			}
		}
	}

	.price{
				padding-bottom:10*@rex;
				overflow: hidden;

				.now,.primary{
					max-width: 45%;
					overflow: hidden;
					display: inline-block;
					margin-left:10*@rex;
					vertical-align: bottom;
					line-height: 1;
				}

				.primary{
					color: #969696;
					text-decoration: line-through;
					max-width: 38%;
				}

				.vip{
					display: block;
					min-width: 20%;
					height: 20*@rex;
				}

				.vip-txt{
					line-height: 1;
					display: inline-block;
					vertical-align: bottom;
					margin-left: 10*@rex;
					color: #E71B2B;
					border: 1*@rex solid #E71B2B;
					padding: 1*@rex 2*@rex;
					border-radius: 3*@rex;
					font-size: 12*@rex;
				}
				.goods-preparing{
					max-width: 38%;
					overflow: hidden;
					white-space: nowrap;
					line-height: 1;
					float: right;
					margin-right: 10*@rex;
					font-size: 13*@rex;
					color: #999;
					position: relative;
					top: 5*@rex;
				}
			}
}
